body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.padding-lr {
  padding: 0 1rem;
}

.mt2 {
  margin-top: 2rem;
}

.mt2 input {
  color: darkorchid;
}

.todo {
  margin-bottom: 1rem;
}
.todo span {
  color: darkmagenta;
}


.todo.completed span{
  text-decoration: line-through;
}

.todo label {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  padding: 1rem;
  background: #f3f3f3;
}

.todo i {
  z-index: 2;
}

.no-todos-title {
  color: darkmagenta;
}
.centered-content {
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}